.App {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

.calculator {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}

.calculator button, .calculator div {
  border: 1px solid black;
  border-radius: 5px;
  font-weight: bold;
  padding: 20px;
  margin: 5px;
  background-color: white;
}

.row {
  grid-column: 1 / 5;
  text-align: right;
  overflow: hidden;
}

.big {
  grid-column: 1 / 3;
}

.equal {
  grid-row: span 2;
}